"use client";

import type { Camera } from '@/lib/types';
import Link from 'next/link';
import Image from 'next/image';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import FavoriteButton from './favorite-button';
import { Badge } from './ui/badge';
import { Skeleton } from './ui/skeleton';
import { useState } from 'react';

export function CameraCard({ camera }: { camera: Camera }) {
    const [isLoading, setIsLoading] = useState(true);

    return (
        <Card className="overflow-hidden transition-all duration-300 hover:shadow-lg hover:border-primary/50">
            <CardHeader className="flex flex-row items-start justify-between gap-4 p-4">
                <div className="space-y-1.5 flex-1">
                    <CardTitle className="text-base leading-snug">
                        <Link href={`/cameras/${camera.id}`} className="hover:underline">
                            {camera.name}
                        </Link>
                    </CardTitle>
                    <CardDescription>
                        <Badge variant="secondary">{camera.region}</Badge>
                    </CardDescription>
                </div>
                <FavoriteButton id={camera.id} />
            </CardHeader>
            <CardContent className="p-0">
                <Link href={`/cameras/${camera.id}`} className="block aspect-video relative">
                    {isLoading && <Skeleton className="absolute inset-0" />}
                    <Image
                        src={camera.imageUrl}
                        alt={`Live feed from ${camera.name}`}
                        fill
                        className={`object-cover transition-opacity duration-300 ${isLoading ? 'opacity-0' : 'opacity-100'}`}
                        sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                        onLoad={() => setIsLoading(false)}
                        data-ai-hint={camera.imageHint}
                    />
                </Link>
            </CardContent>
        </Card>
    );
}
