"use client";

import { useState, useMemo } from 'react';
import type { Camera } from '@/lib/types';
import { Input } from '@/components/ui/input';
import { CameraCard } from '@/components/camera-card';
import { Search } from 'lucide-react';
import { ScrollArea } from '@/components/ui/scroll-area';

export default function CameraList({ cameras }: { cameras: Camera[] }) {
    const [searchTerm, setSearchTerm] = useState('');

    const filteredCameras = useMemo(() => {
        if (!searchTerm) return cameras;
        return cameras.filter(camera =>
            camera.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
            camera.region.toLowerCase().includes(searchTerm.toLowerCase())
        );
    }, [cameras, searchTerm]);

    return (
        <div className="flex flex-col h-full">
            <div className="p-4 border-b border-border sticky top-0 bg-background/95 z-10">
                <div className="relative">
                    <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-muted-foreground" />
                    <Input
                        type="search"
                        placeholder="Search cameras by name or region..."
                        className="pl-10"
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                    />
                </div>
            </div>
            <ScrollArea className="flex-1">
                <div className="p-4 grid gap-4">
                    {filteredCameras.map(camera => (
                        <CameraCard key={camera.id} camera={camera} />
                    ))}
                    {filteredCameras.length === 0 && (
                        <div className="text-center py-10 text-muted-foreground">
                            <p>No cameras found.</p>
                        </div>
                    )}
                </div>
            </ScrollArea>
        </div>
    );
}
